@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  margin: 0;
  padding: 0;
  z-index: 2;
  font-family: sans-serif;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

/* FONT FAMILIES */
h1, h2, h3, h4, h5, a {
  font-family: "Playfair Display", serif;
}

p {
  font-family: "Open Sans", sans-serif;
}

/* FONT SIZES */
h1 {
  font-size: 6vh;
  font-weight: bold;
}

h2 {
  font-size: 5vh;
  font-weight: 500;
}

h3 {
  font-size: 4vh;
  font-weight: 500;
}

h4 {
  font-size: 2.5vh;
  font-weight: bold;
}

h5 {
  font-size: 2.5vh;
}

p {
  font-size: 2vh;
  line-height: 3vh;
}

a {
  font-size: 1.8vh;
}

/* font styles */

p {
  color: #5c677dff;
  font-weight: 400;
  text-align: justify;
}